.work {
  padding-top: 5.1rem;
  padding-bottom: 5.7rem;

  &__item {
    text-align: center;

    &:not(:last-child) {
      @media (max-width: 768px) {
        margin-bottom: 2rem;
      }
    }
  }

  &__list {
    margin-top: 3.7rem;

    @media (max-width: 768px) {
      flex-direction: column;
    }
  }

  &__image {
    margin-bottom: 2.5rem;
  }

  &__text {
    margin-top: 1.5rem;
    text-align: justify;
    text-align-last: center;
  }
}
